import uniList from "@/components/uni-list/uni-list.vue"
import uniListItem from "@/components/uni-list-item/uni-list-item.vue"
export default {
	components: {
		uniList,
		uniListItem,
	},
	data() {
		return {
			title: '添加产品类型',
			formData: {
				name: '',
				remarks: ''
			},
			disableSubmit: true
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack()
		},
		formSubmit() {
			uni.navigateBack()
		}
	},
	watch:{
		formData: {
			handler(val) {
				if (val.name) {
					this.disableSubmit = false
				} else {
					this.disableSubmit = true
				}
			},
			deep: true
		}
	}
}
